import React from "react";
import wrapper from "../../../commons/WebComponentWrapper";
import StepConnector from "@mui/material/StepConnector";

const dispatchedEvents = [];

const SrsMuiStepConnector = function (props, ref) {
  return <StepConnector {...props} theme={props.muiTheme}/>;
};

const styles = `
  /* Custom styles for the MuiStepConnector component */
`;

const props = [
  {
    name: "active",
    defaultValue: false,
    type: "boolean",
  },
  {
    name: "alternativeLabel",
    defaultValue: false,
    type: "boolean",
  },
  {
    name: "completed",
    defaultValue: false,
    type: "boolean",
  },
  {
    name: "disabled",
    defaultValue: false,
    type: "boolean",
  },
  {
    name: "index",
    defaultValue: 0,
    type: "number",
  },
  {
    name: "orientation",
    defaultValue: "horizontal",
    type: "string",
  },
  {
    name: "position",
    defaultValue: "left",
    type: "string",
  },
  {
    name: "sx",
    defaultValue: {},
    type: "object"
  }
];

const slots = [
  {
    name: "default",
    allowed: [],
  },
];

const WrappedComponent = wrapper(
  React.forwardRef(SrsMuiStepConnector),
  props,
  "mui"
);
export { WrappedComponent, styles, props, dispatchedEvents, slots };
